import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
// @mui
import { Container, Backdrop, CircularProgress, Card, Table, TableBody, TableRow, TableCell, Typography, Box, Divider, Grid } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import Image from '../../components/Image';
// sections
import { InvoiceToolbar } from '../../sections/@dashboard/invioce/result';
import axios from '../../utils/axios';
import { fCurrency } from '../../utils/formatNumber';
import useAuth from '../../hooks/useAuth';

// ----------------------------------------------------------------------

export default function ResultInvoice() {
  const { themeStretch } = useSettings();
  const { name = '' } = useParams();
  const [trans, setTrans] = useState(null);
  const [load, setLoad] = useState(true);
  const { setting } = useAuth();

  useEffect(() => {
    setLoad(true);
    axios.get(`/api/resultchecker/trans/${name}/secure`)
      .then(response => {
        setTrans(response.data?.trans);
        setLoad(false);
      })
      .catch(() => setLoad(false));
  }, [name]);

  return (
    <Page title="Receipt: Result Checker">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Transaction Receipt"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.general.app },
            { name: 'Transactions', href: PATH_DASHBOARD.general.transactions },
            { name: 'Receipt' },
          ]}
        />

        {!load && trans && (
          <>
            <InvoiceToolbar invoice={trans} />
            <Card sx={{ pt: 5, px: 5, pb: 5 }}>
              <Grid container>
                <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
                  <Image disabledEffect visibleByDefault alt="logo" src="/upload/welcome.png" sx={{ maxWidth: 120 }} />
                </Grid>

                <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
                  <Box sx={{ textAlign: { sm: 'right' } }}>
                    <Label color={trans?.plan_status === 1 ? 'success' : 'error'} sx={{ textTransform: 'uppercase', mb: 1 }}>
                      {trans?.plan_status === 1 && 'successful' || trans?.plan_status === 0 && 'proccessing' || trans?.plan_status === 2 && 'failed'}
                    </Label>
                    <Typography variant="h6">{trans?.transid}</Typography>
                  </Box>
                </Grid>
              </Grid>

              <Typography variant="overline" sx={{ color: 'text.secondary', mb: 2, display: 'block' }}>
                Result Checker Invoice Details
              </Typography>

              {/* Parsed Cards Layout */}
              <Grid container spacing={3}>
                {(() => {
                  let cards = [];
                  if (trans?.purchase_code) {
                    // 1. Handle "pin1 => VAL pin2 => VAL" format (Easy Provider)
                    let cleanCode = trans.purchase_code.replace(/pin\d+\s*=>/gi, ' ');

                    // 2. Split by Comma OR Whitespace (handling Self provider's space separator)
                    const rawItems = cleanCode.split(/[,\s]+/);

                    cards = rawItems.map(item => {
                      let pin = item.trim();
                      let serial = '';

                      // Robust regex to split PIN and Serial (matches <==>, <==, ==>, etc)
                      const splitRegex = /\s*<={1,2}>{0,1}\s*|\s*={2,}>\s*/;
                      if (splitRegex.test(pin)) {
                        const parts = pin.split(splitRegex);
                        pin = parts[0].trim();
                        if (parts.length > 1) {
                          serial = parts[1].trim();
                        }
                      }
                      return { pin, serial };
                    }).filter(c => c.pin.length > 0); // Filter out empty entries
                  }

                  return cards.map((card, index) => (
                    <Grid item xs={12} md={6} key={index}>
                      <Card sx={{ p: 3, border: '1px solid #E5E7EB', display: 'flex', flexDirection: 'column', gap: 2, position: 'relative', overflow: 'visible' }}>
                        {/* Header: Exam Name & Logo */}
                        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                          <Box>
                            <Typography variant="h5" sx={{ fontWeight: 'bold' }}>{trans?.exam_name}</Typography>
                            <Typography variant="body2" sx={{ color: 'text.secondary' }}>Result Checker</Typography>
                          </Box>
                          <Box
                            sx={{
                              width: 48,
                              height: 48,
                              borderRadius: 1,
                              overflow: 'hidden',
                            }}
                          >
                            <Image
                              alt="logo"
                              src={(trans?.exam_name === 'WAEC' || trans?.exam_name === 'H-WAEC') && `/upload/waec.png` ||
                                trans?.exam_name === 'NECO' && `/upload/neco.jpg` ||
                                trans?.exam_name === 'NABTEB' && `/upload/nabteb.png` ||
                                trans?.exam_name === 'NBAIS' && `/upload/nbais.png`}
                              sx={{ width: '100%', height: '100%', objectFit: 'contain' }}
                            />
                          </Box>
                        </Box>

                        <Divider sx={{ borderStyle: 'dashed' }} />

                        {/* Token Section */}
                        <Box sx={{ textAlign: 'center', py: 1 }}>
                          <Typography variant="caption" sx={{ color: 'text.secondary', textTransform: 'uppercase', letterSpacing: 1 }}>
                            Token / PIN
                          </Typography>
                          <Box sx={{ bgcolor: themeStretch ? 'transparent' : 'background.neutral', py: 1.5, borderRadius: 1, mt: 1 }}>
                            <Typography variant="h3" sx={{ color: 'primary.main', letterSpacing: 2 }}>
                              {card.pin}
                            </Typography>
                          </Box>
                        </Box>

                        {/* Serial Number Section */}
                        {card.serial && (
                          <Box sx={{ textAlign: 'center' }}>
                            <Typography variant="caption" sx={{ color: 'text.secondary', textTransform: 'uppercase', letterSpacing: 1 }}>
                              Serial Number
                            </Typography>
                            <Typography variant="subtitle1" sx={{ fontWeight: 'bold', mt: 0.5 }}>
                              {card.serial}
                            </Typography>
                          </Box>
                        )}

                        <Divider sx={{ borderStyle: 'dashed' }} />

                        {/* Footer Instruction */}
                        <Box sx={{ textAlign: 'center' }}>
                          <Typography variant="caption" sx={{ color: 'text.secondary' }}>
                            Usage: Visit <strong>{trans?.exam_name}</strong> Portal to check results
                          </Typography>
                        </Box>
                      </Card>
                    </Grid>
                  ));
                })()}
              </Grid>

              <Table sx={{ minWidth: 200, mt: 4 }}>
                <TableBody>
                  <TableRow>
                    <TableCell sx={{ fontWeight: 'bold' }}>Quantity</TableCell>
                    <TableCell>{trans?.quantity}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell sx={{ fontWeight: 'bold' }}>Amount</TableCell>
                    <TableCell>₦{fCurrency(trans?.amount)}</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell sx={{ fontWeight: 'bold' }}>Date</TableCell>
                    <TableCell>{trans?.plan_date}</TableCell>
                  </TableRow>
                </TableBody>
              </Table>

              <Divider sx={{ mt: 5 }} />

              <Grid container>
                <Grid item xs={12} md={9} sx={{ py: 3 }}>
                  <Typography variant="subtitle2">NOTES</Typography>
                  <Typography variant="body2">
                    We appreciate your continuous patronage. Your satisfaction is our top priority!
                  </Typography>
                </Grid>
                <Grid item xs={12} md={3} sx={{ py: 3, textAlign: 'right' }}>
                  <Typography variant="subtitle2">any enquiry, kindly contact us @</Typography>
                  <Typography variant="body2">{setting?.general?.app_email}</Typography>
                </Grid>
              </Grid>
            </Card>
          </>
        )}

        <Backdrop
          sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
          open={load}
        >
          <CircularProgress color="inherit" />
        </Backdrop>
      </Container>
    </Page>
  );
}
