/* eslint-disable camelcase */
import PropTypes from 'prop-types';
import { Page, View, Text, Image, Document } from '@react-pdf/renderer';
// utils
import { fCurrency } from '../../../../utils/formatNumber';
//
import styles from './InvioceStyle';


// ----------------------------------------------------------------------

// ----------------------------------------------------------------------

InvoicePDF.propTypes = {
  invoice: PropTypes.object.isRequired,
};

export default function InvoicePDF({ invoice }) {
  // eslint-disable-next-line camelcase
  const { plan_status, transid, exam_name, quantity, plan_date, purchase_code, amount, oldbal, newbal } = invoice;

  // Parse PINs and Serials from purchase_code
  // Expected format: "PIN1,PIN2" or "PIN1<==>SERIAL1, PIN2<==>SERIAL2" or just "PIN"
  let cards = [];
  if (purchase_code) {
    // 1. Handle "pin1 => VAL pin2 => VAL" format (Easy Provider)
    let cleanCode = purchase_code.replace(/pin\d+\s*=>/gi, ' ');

    // 2. Split by Comma OR Whitespace (handling Self provider's space separator)
    const rawItems = cleanCode.split(/[,\s]+/);

    cards = rawItems.map(item => {
      let pin = item.trim();
      let serial = '';

      // Robust regex to split PIN and Serial (matches <==>, <==, ==>, etc)
      const splitRegex = /\s*<={1,2}>{0,1}\s*|\s*={2,}>\s*/;
      if (splitRegex.test(pin)) {
        const parts = pin.split(splitRegex);
        pin = parts[0].trim();
        if (parts.length > 1) {
          serial = parts[1].trim();
        }
      }
      return { pin, serial };
    }).filter(c => c.pin.length > 0);
  }

  return (
    <Document>
      <Page size="A4" style={styles.page}>
        <View style={[styles.gridContainer, styles.mb40]}>
          <Image source={`${window.location.origin}/upload/welcome.png?v=2`} style={{ height: 32 }} />
          <View style={{ alignItems: 'flex-end', flexDirection: 'column' }}>
            <Text style={styles.h3}>{plan_status === 1 && 'successful' || plan_status === 0 && 'proccessing' || plan_status === 2 && 'failed'}</Text>
            <Text>{transid}</Text>
          </View>
        </View>

        <Text style={[styles.overline, styles.mb8]}> Result Checker Invoice Details</Text>

        <View style={styles.row}>
          {cards.map((card, index) => (
            <View style={styles.col} key={index}>
              <View style={styles.card}>
                {/* Header */}
                <View style={styles.toplogo}>
                  <View style={{ width: '70%' }}>
                    <Text style={styles.h4}>{exam_name}</Text>
                    <Text style={styles.h3}>Result Checker</Text>
                  </View>
                  <Image
                    source={
                      (exam_name === 'WAEC' || exam_name === 'H-WAEC') && `${window.location.origin}/upload/waec.png?v=2` ||
                      exam_name === 'NECO' && `${window.location.origin}/upload/neco.jpg?v=2` ||
                      exam_name === 'NABTEB' && `${window.location.origin}/upload/nabteb.png?v=2` ||
                      exam_name === 'NBAIS' && `${window.location.origin}/upload/nbais.png?v=2`
                    }
                    style={{ height: 35, width: 35, borderRadius: 5 }}
                  />
                </View >

                {/* Token / PIN */}
                <Text style={{ fontSize: 8, color: '#637381', marginTop: 8 }}>TOKEN / PIN</Text>
                <View style={styles.pin}>
                  <Text style={{ "fontWeight": "bold", "fontSize": "20px", "letterSpacing": 2 }}> {card.pin} </Text>
                </View>

                {/* Serial Number */}
                {card.serial ? (
                  <View style={{ marginTop: 4 }}>
                    <Text style={{ fontSize: 8, color: '#637381' }}>SERIAL NUMBER</Text>
                    <Text style={{ fontSize: 10, fontWeight: 'bold' }}>{card.serial}</Text>
                  </View>
                ) : null}

                {/* Footer Message */}
                <Text style={{ fontSize: 8, marginTop: 10, textAlign: 'center', color: '#637381' }}>
                  Visit {exam_name} Portal to check results
                </Text>

              </View>
            </View>
          ))}
        </View>

        <View style={[styles.gridContainer, styles.footer]}>
          <View style={styles.col8}>
            <Text>Date: {plan_date}</Text>
            <Text>Amount: ₦{fCurrency(amount)} | Old Bal: ₦{fCurrency(oldbal)} | New Bal: ₦{fCurrency(newbal)}</Text>
            <Text style={[styles.subtitle2, { marginTop: 10 }]}>NOTES</Text>
            <Text> We appreciate your continuous patronage. Your satisfaction is our top priority!</Text>
          </View>
        </View>
      </Page>
    </Document>
  );
}
